
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video,
input {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font-weight: normal;
  vertical-align: baseline;
  box-sizing: border-box;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
menu,
nav,
section {
  display: block;
}
html {
  height: 100%;
}
body {
  line-height: 1;
  background: #d9f0ff;
  height: auto;
  min-height: 100%;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  color: #7e8c8d;
  -webkit-backface-visibility: hidden;
  text-decoration: none;
}
li {
  list-style: none;
}
body {
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
button {
  height: 34px;
  line-height: 34px;
  padding: 0 20px;
  background: #fff;
  border-width: 1px;
  border-style: solid;
  border-color: #d9d9d9;
  margin-right: 10px;
}
button .btn-orange {
  color: #fff;
  background: #fa7d3c;
  background: linear-gradient(top bottom, #fa7d3c 0%, #f55f10 100%);
}
button .btn-orange:hover {
  background: #f4722e;
  background: linear-gradient(top bottom, #f4722e 0%, #f05807 100%);
}
button .btn-default {
  box-shadow: 0px 1px 2px rgba(0,0,0,0.3);
}
button .btn-default:hover {
  box-shadow: 0px 2px 2px rgba(0,0,0,0.3);
}
a {
  color: #2c3e50;
  text-decoration: none;
}
input,
button[disabled] {
  cursor: not-allowed;
}
.container {
  width: 1336px;
  margin: 0 auto;
}
.block {
  background-color: #fff;
  margin: 15px 0;
  padding: 20px;
  width: 100%;
  height: auto;
}
button {
  width: 82px;
}
.btn-default {
  box-shadow: 0px 1px 2px rgba(0,0,0,0.1);
}
.btn-defaulthover {
  box-shadow: 0 1px 2px rgba(0,0,0,0.15);
}
.btn-orange {
  color: #fff;
  background: #fa7d3c;
  background: linear-gradient(top bottom, #fa7d3c 0%, #f55f10 100%);
}
.btn-orangehover {
  background: #f4722e;
  background: linear-gradient(top bottom, #f4722e 0%, #f05807 100%);
}
.link {
  color: #87cefa;
}
.img-content {
  width: 100%;
  display: inline-block;
  border: 1px solid #e5e9ef;
  border-radius: 4px;
  padding: 2px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
.img-content-sm {
  display: inline-block;
  width: 104px;
  height: 104px;
  border: 1px solid #e5e9ef;
  border-radius: 4px;
  padding: 2px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-right: 16px;
}
.text-ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.text-ellipsis-mul {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: break;
  display: -webkit-box;
  -webkit-line-clamp: 2;
}
.waterfall {
  margin-top: 15px;
  column-count: 6;
  column-gap: 1em;
}
.waterfall .fallitem {
  padding: 1em;
  margin: 0 0 15px 0;
  break-inside: avoid;
  background: #fff;
  box-shadow: 2px 2px 2px rgba(0,0,0,0.3);
  width: 100%;
  height: 100%;
  display: block;
}
.waterfall .fallitemhover {
  box-shadow: 2px 2px 6px rgba(0,0,0,0.4);
}
.waterfall .fallitem img {
  width: 100%;
}
.waterfall .fallitem .fallcover {
  line-height: 1.4em;
}
.waterfall .fallitem .fallcover .img-author {
  width: 100%;
  text-align: center;
}
#app {
  height: 100%;
  background-image: url("/dist/static/images/bg.jpg");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: contain;
  color: #2c3e50;
  padding: 15px 0;
  background-color: #d9f0ff;
}


